<template>
  <div v-if="userId != null" class="about">
    <form @submit.prevent="fnSignOut">
    <h2 style="font-family: 나눔고딕">{{username}}님 반갑습니다!</h2>
    <!--
    <h2>{{nameList}}</h2>
    -->
    <h2>{{userId}}></h2>
    <p>
    <button class="w3-button w3-green w3-round" @click="$router.push('/login')">LogOut</button>
    </p>
    <p>
      <button type="submit" class="w3-button w3-green w3-round">SignOut</button>
    </p>
    </form>
  </div>
  <div v-if="userId == null" class="about">
    <h2>Home</h2>
    <button class="w3-button w3-green w3-round" @click="$router.push('/login')">LogIn</button>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data(){
    return{
      nameList: [],
      userId : '',
      username : ''
    };
  },
  props:{
    msg: String,
  },
  created() {
    this.getParam();
    this.getTest();
  },
  methods:{
    getParam(){
      this.userId = this.$route.query.userId;
      console.log('userId : ' + this.$route.query.userId);
      this.username = this.$route.query.username;
      console.log('username : ' + this.$route.query.username);

    },
    getTest(){
      axios.get("/about")
      .then(response=>{
        console.log(response.data);
        this.nameList = response.data;
      })
      .catch(err=>{
        console.log(err.response);
      });
    },
    fnSignOut() {
      if(confirm('탈퇴 하시겠습니까?')) {
        axios.delete(`/about/delete`, {
          data: {
            userId: this.userId,
          },
        })
            .then(response=>{
              console.log('response.data : ' + response.data);
              alert('탈퇴가 완료되었습니다.')
              this.$router.push('/login');
            })
            .catch(err=>{
              console.log(err.response);
            });
      }
    }
  },
};

</script>
